export default {
	props: {
		bannerList: {
			type: Array,
			default () {
				return []
			}
		},
		swiperConfig: {
			type: Object,
			default () {
				return {
					indicatorDots: true,
					indicatorColor: 'rgba(255, 255, 255, .4)',
					indicatorActiveColor: 'rgba(255, 255, 255, 1)',
					autoplay: false,
					interval: 3000,
					duration: 300,
					circular: true,
					previousMargin: '58rpx',
					nextMargin: '58rpx'
				}
			}
		},
		scaleX: {
			type: String,
			default: (634 / 550).toFixed(4)
		},
		scaleY: {
			type: String,
			default: (378 / 328).toFixed(4)
		}
	},
	computed: {
		listLen() {
			return this.bannerList.length
		}
	},
	data() {
		return {
			curIndex: 0,
			descIndex: 0,
			isDescAnimating: false
		}
	},
	methods: {
		swiperChange(e) {
			const that = this
			this.curIndex = e.mp.detail.current
			this.isDescAnimating = true
			let timer = setTimeout(function() {
				that.descIndex = e.mp.detail.current
				clearTimeout(timer)
			}, 150)
		},
		animationfinish(e) {
			this.isDescAnimating = false
		},
		getBannerDetail(index) {
			uni.showLoading({
				title: '将前往详情页面',
				duration: 2000,
				mask: true
			})
		}
	}
}
